import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TouchApp from '@material-ui/icons/TouchApp';

const styles = theme => ({
  item: {
    padding: theme.spacing.unit * 3
  },
  imgClass: {
    maxWidth: '100%',
    transition: 'opacity ease-in-out .3s'
  },
  imageContainer: {
    position: 'relative'
  },
  icon: {
    position: 'absolute',
    transition: '.2s cubic-bezier(0.68, 0, 0.27, 2.55)',
    fontSize: '2rem',
    padding: '1rem',
    boxSizing: 'content-box',
    color: '#fbc00c',
    cursor: 'pointer',
    '&:hover': {
      transform: 'scale(.9)'
    }
  }
});

const SingleSmallProject = props => {
  const { classes, name, details, img, live } = props;

  return (
    <React.Fragment>
      <Grid className={classes.item} item xs={6}>
        <div className={classes.imageContainer}>
          <a href={live} target="_blank" rel="noopener noreferrer">
            <TouchApp className={classes.icon} />
          </a>
          <img
            className={classes.imgClass}
            src={require(`../../../../assets/images/${img}.jpg`)}
            alt=""
          />
        </div>
        <Typography variant="body1">{name}</Typography>
        <Typography color="textSecondary" variant="body2">
          {details}
        </Typography>
      </Grid>
    </React.Fragment>
  );
};

export default withStyles(styles)(SingleSmallProject);
