import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import ButtonAnimated from '../ButtonAnimated/ButtonAnimated';
import ExpandMore from '@material-ui/icons/ExpandMore';

const styles = theme => ({
  root: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
    marginTop: '3rem',
    [theme.breakpoints.down('xs')]: {
      padding: theme.spacing.unit * 3
    },
    [theme.breakpoints.up('md')]: {
      width: '80vw',
      marginLeft: 'auto',
      marginRight: 'auto'
    }
  },
  item: {
    margin: theme.spacing.unit
  },
  imgClass: {
    maxWidth: '100%',
    transition: 'opacity ease-in-out .2s',
    // cursor: 'pointer',
    '&:hover': {
      // opacity: 0.9
    }
  },
  secret: {
    transition: '.3s cubic-bezier(0.68, 0, 0.27, 2.55)',
    height: 0,
    opacity: 0
  },
  clicked: {
    height: '4rem',
    opacity: 1
  },
  imageContainer: {
    position: 'relative'
  },
  icon: {
    [theme.breakpoints.down('sm')]: {
      fontSize: '4rem'
    },
    position: 'absolute',
    transition: '.2s cubic-bezier(0.68, 0, 0.27, 2.55)',
    fontSize: '12rem',
    boxSizing: 'content-box',
    color: '#fbc00c',
    cursor: 'pointer',
    '&:hover': {
      transform: 'translateY(2rem)'
    }
  },
  iconFlip: {
    [theme.breakpoints.down('sm')]: {
      fontSize: '4rem'
    },
    transform: 'rotateX(.5turn)',
    position: 'absolute',
    transition: '.2s cubic-bezier(0.68, 0, 0.27, 2.55)',
    fontSize: '12rem',
    boxSizing: 'content-box',
    color: '#fbc00c',
    cursor: 'pointer',
    '&:hover': {
      transform: 'translateY(-2rem) rotateX(.5turn)'
    }
  }
});

class ProjectBig extends Component {
  state = {
    clicked: null
  };

  render() {
    const {
      classes,
      category,
      name,
      details,
      technologies,
      secret,
      img,
      repo,
      live
    } = this.props;
    return (
      <React.Fragment>
        <div className={classes.root}>
          <Grid
            container
            direction="row"
            justify="space-between"
            alignItems="flex-end"
            spacing={8}
          >
            <Grid className={classes.item} item sm={5} xs={12}>
              <Typography variant="h4" component="h4">
                {name}
              </Typography>
              <Typography variant="body1">{category}</Typography>
              <Typography color="textSecondary" variant="body2">
                {details}
              </Typography>
            </Grid>
            <Grid className={classes.item} item sm={5} xs={12}>
              <Typography variant="body1">Technologies</Typography>
              <Typography color="textSecondary" variant="body2">
                {technologies}
              </Typography>
            </Grid>
            <Grid
              item
              xs={12}
              className={
                this.state.clicked
                  ? `${classes.secret} ${classes.clicked}`
                  : classes.secret
              }
            >
              <div>
                <ButtonAnimated label={'Live'} url={live} />
                <ButtonAnimated label={'GitHub'} url={repo} />
              </div>
            </Grid>
          </Grid>

          <Grid
            container
            direction="row"
            justify="center"
            alignItems="stretch"
            spacing={24}
          >
            <Grid className={classes.item} item sm={12} xs={12}>
              <div className={classes.imageContainer}>
                <ExpandMore
                  onClick={() =>
                    this.setState({ clicked: !this.state.clicked })
                  }
                  className={
                    this.state.clicked ? classes.iconFlip : classes.icon
                  }
                />
                <img
                  className={classes.imgClass}
                  src={require(`../../../assets/images/${img}.jpg`)}
                  alt=""
                />
              </div>
            </Grid>
          </Grid>
        </div>
      </React.Fragment>
    );
  }
}

export default withStyles(styles)(ProjectBig);
