import React from 'react';
import { withStyles } from '@material-ui/core/styles';

const styles = theme => ({
  btn: {
    // display: 'inline-block',
    fontWeight: 'bold',
    padding: '.5rem',
    marginRight: '2rem',
    cursor: 'pointer',
    border: '1px solid #fbc00c',
    backgroundColor: '#fbc00c',
    color: 'white',
    // position: 'absolute',
    '&:hover': {
      backgroundColor: 'white',
      color: '#fbc00c'
    }
  }
});

const ButtonAnimated = props => {
  const { classes } = props;
  return (
    <a
      className={classes.btn}
      href={props.url}
      target="_blank"
      rel="noopener noreferrer"
    >
      {props.label}
    </a>
  );
};

export default withStyles(styles)(ButtonAnimated);
